.timetable {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .fc .fc-toolbar-title {
    font-size: 1.5em;
    font-weight: bold;
  }
  
  .fc .fc-button {
    background-color: #007bff;
    color: #fff;
    border: none;
  }
  
  .fc .fc-button:hover {
    background-color: #0056b3;
  }
  